import React, { Component } from 'react';
import './App.css';
import Header from './Header/Header.component';
import {BrowserRouter,Route} from 'react-router-dom';
import Home from './Home/Home.components';
import MovieDetail from './Movie/MovieDetail.component';

class App extends Component {
  
render(){

  return (
    <BrowserRouter>
    <div className="App">
      <Header/>
      <Route exact path="/movie" component={Home}></Route>
      <Route path="detail/movie/:movieid" component={MovieDetail}></Route>
    </div>
    </BrowserRouter>
  );

 
}
}

export default App;
