import React ,{Component }from 'react';
import MovieList from '../Movie/MovieList.component';
class Movie extends Component {

render(){

    if(this.props.children!==undefined){
        return (
            
            this.props.children.map((movieslist)=>{
               return (
                   <MovieList year = {movieslist.year} rating={movieslist.rating} genre= {movieslist.genres} key={movieslist.id} movieid = {movieslist.id} image={movieslist.medium_cover_image} urldata={movieslist.url}  >{movieslist.title}</MovieList>
               )
               })
                   )
    }else{
        return(
            <div>NO Movie Found</div>
        )
    }
    
 
}

}

export default Movie;