import React, { Component } from 'react';
import './Movie.styles.css';
class MovieList extends Component {

    render()
    {
       return(
        <div className="col-md-3 mt-3">
            <div className="card " >
        <img className="img-fluid img-thumbnail ImgHieght card-img-top w-100"  src={this.props.image} alt={this.props.children}/>
      </div>
      <div className=" TextColor">
        <ul className="">
        <li className="font-weight-bold"><a href={"detail/movie/"+this.props.movieid}>{this.props.children.length>20?this.props.children.substring(0,20)+'..':this.props.children}</a></li>
       <li>{this.props.year}({this.props.rating}/10)</li>
          </ul>
      </div>

        </div>
        
       )
        
    }

        
           
        
    }

export default MovieList;